import React, { useState, useEffect } from "react";

import "./App.css";
import ControlledCarousel from "./Components/Carousel/carousel";
import NavBar from "./Components/NavBar/navBar";
import Search from "./Components/Search/search";
import ResultSearchCard from "./Components/ResultCard/ResultSearchCard";
import Navigation from "./Components/Navigation/navigationActive";
import { FetchData, FetchMoviesInTheatres } from "./Api/api.js";

function App() {
  const [movies, setMovies] = useState([]);
  const [isLoading, setIsLoading] = useState(false);
  const [search, setSearch] = useState([]);
  const [searchedMovies, setSearchedMovies] = useState([]);
  const [moviesInTheater, setMoviesInTheaters] = useState([]);
  const [favorites, setFavorites] = useState();
  const [isError, setError] = useState(null);

  const SearchData = (search) => {
    setSearch(search);
    console.log(search);
  };

  useEffect(() => {
    setIsLoading(true);
    (async function () {
      try {
        const response = await fetch(
          `https://api.themoviedb.org/3/search/movie?api_key=45bf6592c14a965b33549f4cc7e6c664&query=${search}`
        );
        const json = await response.json();
        setSearchedMovies(json.results);
      } catch (e) {
        setError(e);
      }
      setIsLoading(false);
    })();
  }, [search]);

  useEffect(() => {
    setIsLoading(true);
    try {
      FetchData().then((result) => setMovies(result));
      FetchMoviesInTheatres().then((result) => setMoviesInTheaters(result));
    } catch (e) {}
  }, []);

  return (
    <div className="App">
      <NavBar SearchData={SearchData} />

      <section id="Busqueda">
        {search.length > 3 ? (
          <ResultSearchCard movies={searchedMovies} />
        ) : (
          <section id="popular">
            <Navigation />
            <ControlledCarousel movies={movies}  />
            <div
              class="theater-section"
             
            >
              <strong>In Theathers!</strong>
            </div>
            <section>
              <div  className="">
            <ControlledCarousel movies={moviesInTheater} />
            </div>
            </section>
          </section>
        )}
      </section>
    </div>
  );
}

export default App;
